.top-bar {
  padding-top: 25px;

  display: flex;
  justify-content: space-between;
  align-items: center;

  @include onTablet {
    padding-top: 50px;
  }

  &__icons {
    display: flex;
    gap: 55px;
  }

  &__logo-link {
    display: flex;
  }

  &__logo {
    width: 64px;
    height: 46px;

    @include hover (transform, scale(1.2));

    @include onTablet {
      width: 87px;
      height: 65px;
    }
  }
}
